import { OptionValues } from 'commander';
import { buildPackage, Output } from '../../lib/builder';

export default async (opts: OptionValues) => {
  await buildPackage({
    outputs: new Set([Output.esm, Output.types]),
    minify: opts.minify,
    useApiExtractor: opts.experimentalTypeBuild,
  });
};
